*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
  list-style-type:none;
}
.Header{
  background-color: rgb(228, 198, 79);
  display:flex;
  align-items:center;
}

.Footer{
  display: flex;
  justify-content: center;
  font-size: 2rem;
  margin-top:42vh;
  height:50px;
  padding-bottom: 20px;
}
.main1{
  margin-top: 3%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.main2{
  width:30vw;
  border-radius: 30px;
  height: 38vh;
  padding-right: 1%;
  box-shadow: 4px 4px 10px rgb(56, 55, 55);
  text-align: end;
  position: relative;
}
.btn{
  position: absolute !important;
  bottom:5% !important;
  right:4% !important;
}
.inp1{
  border: none;
  width:97%;
  margin-top: 6%;
  height:6vh;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 10px;
  outline: none;
  text-align:center;
  overflow-x: auto;
  border-radius: 10px;
}
.inp2{
  width:97%;
  height:17vh;
  border: none;
  font-size: 1.2rem;
  padding-top: 10px;
  outline: none;
  border-radius: 10px;
  resize: none;
  overflow: auto;
}
.btn:hover{
  background-color: rgb(4, 255, 0) !important;

}
.icon:hover{
  color: white !important;
}
.note{
  width: 23%;
  height:53%;
  margin-left: 2%;
  margin-top: 1%;
  margin-bottom: 1%;
  border-radius: 20px;
  min-height: 41vh;
  box-shadow:  0px 4px 9px  black;
  position: relative;
}
.note h1{
  background-color: yellow;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  padding-top: 3%;
  padding-left: 2%;
  padding-right: 1%;
  max-height:10vh;
  min-height: 5vh;
  overflow-x: auto;
}
.note p{
  margin-left: 2.5%;
  margin-right: 2.5%;
  font-size: 1.2rem;
  max-height: 20vh;
  min-height: 10vh;
  overflow: auto;
}
.btn1{
  background-color: white !important;
  box-shadow: 1px 1px 4px black;
  position: absolute !important;
  right:4%;
  bottom: 4%;
}
.btn1:hover{
  background-color: rgb(255, 0, 0) !important;
}
.btn2:hover{
  color: white !important;
}
.btn2{
  color: rgb(255, 0, 0);
  font-size: 1.9vw !important;
}
.box1{
  margin-top: 2%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 1%;
}
@media (max-width:1000px){
  .btn2{
    font-size:3vw !important;
  }
  .note{
    width: 45%;
    min-height: 42vh;
  }
  .main2{
    width:60vw;
    height: 39vh;
  }
}
@media (max-width:660px){

  .btn1{
    margin-left: 90% !important;
  }
  .note{
    width: 80%;
    min-height: 41vh;
  }
  .btn2{
    font-size: 4vw !important;
  }
  .main2{
    width:90vw;
    height: 39vh;
  }
}